.col {
    display: inline-block;
    vertical-align: top;
    padding: 20px 20px;
}

.Group {
    display: table;
    margin: 0 0 20px 0;
}
.GroupMember {
    display: table-row;
}
.GroupMember > * {
    display: table-cell;
    padding: 2px 5px;
}
.GroupMember .group-idx {
    width: 20px;
}
.GroupMember .name {
    width: 200px;
}
.GroupMember .role {
    width: 150px;
    font-style: italic;
}


